<template>
  <div>
    <el-container class="el-container-box1">
      <el-aside width="150px">
        <el-menu
          router
          unique-opened
          default-active="2"
          class="el-menu-vertical-demo"
          background-color="#20222a"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-menu-item index="/index/home">
            <i class="el-icon-menu"></i>
            <span slot="title">首页</span>
          </el-menu-item>

          <!-- 动态侧边栏 -->
          <div v-for="item in userInfo.menus" :key="item.id">
            <el-submenu :index="item.id+''" v-if="item.children">
              <template slot="title">
                <i class="item.icon"></i>
                <span>{{item.title}}</span>
              </template>
              <el-menu-item-group>
                <el-menu-item
                  :index="'/index'+i.url"
                  v-for="i in item.children"
                  :key="i.id"
                >{{i.title}}</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-menu-item :index="'/index'+item.url" v-else>
              <span slot="title">{{item.title}}</span>
            </el-menu-item>
          </div>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header>
          <div class="el-header-div">
            <span>{{userInfo.username}}</span>
            <el-button type="primary" @click="logOut">退出</el-button>
          </div>
        </el-header>
        <el-main>
          <el-breadcrumb separator-class="el-icon-arrow-right" v-if="$route.name">
            <el-breadcrumb-item :to="{ path: '/index/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>{{$route.name}}</el-breadcrumb-item>
          </el-breadcrumb>
          <div class="el-main-view-div">
            <router-view></router-view>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters({
      userInfo: "userInfo"
    })
  },
  components: {},
  data() {
    return {};
  },
  methods: {
    logOut() {
      this.changeUserInfoAction({});
      this.$router.push("/login");
    },
    // handleOpen(key, keyPath) {
    //   console.log(key, keyPath);
    // },
    // handleClose(key, keyPath) {
    //   console.log(key, keyPath);
    // }
    ...mapActions({
      changeUserInfoAction: "changeUserInfoAction"
    })
  },
  mounted() {}
};
</script>
<style scoped>
.el-container-box1 {
  width: 100vw;
  height: 100vh;
}
.el-header {
  height: 53px;
  background-color: #b3c0d1;
  color: #333;
  /* text-align: center; */
  line-height: 60px;
}

.el-header-div {
  float: right;
}

.el-aside {
  height: 100vh;
  background-color: #20222a;
  color: #333;
  /* text-align: center; */
  line-height: 200px;
  overflow-x: hidden;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  /* text-align: center;
  line-height: 160px; */
}
.el-menu-vertical-demo {
  text-align: left;
  width: 150px;
  overflow-x: hidden;
}
.el-main-view-div {
  margin-top: 21px;
}
</style>


